import React, { ReactElement, FormEvent, useState, useCallback, useMemo, useEffect, createRef, RefObject, useRef, useContext } from 'react';
import CSV from 'papaparse';
import url from 'url';
import { Player, ID, TournamentSettings, FetchECFParams } from '../../interfaces';
import { GET, POST, server, FORM} from '../../utils/requests';

import styles from '../../css/sidebar.module.css';
import TabSection from '../../components/TabSection';
import { EditableForm } from '.'
import Search, { SearchProps } from '../../components/Search';
import ECF from 'ecf';
import { SearchProfile } from 'ecf/lib/interfaces';
import { isElectron } from '../../utils/electron';
import { AlertContext } from '../../Contexts';
import { convertToFIDE } from '../../resources/rating';
import { ecf } from '../../assets/icons';
import { FaPlusSquare } from 'react-icons/fa';

interface SidebarProps {
	playerDict: Map<ID, Player>
	playerDictKey: string //required to force a reload
	updatePlayerDict: (force?: boolean) => void
	id: string
	settings: TournamentSettings
	stateKey: string

}

export default function NewPlayer(props: SidebarProps & {
	setPlayers: (refresh?: boolean) => Promise<void>
}): ReactElement {

	/** UPDATES SECTION */

	/** CALLBACK SECTION */

	const requireLastName = useMemo(() => props.settings.competitors === 'individual', [props.settings.competitors]);

	/* RENDERING SECTION */

	const handlePaste = useCallback((e: React.ClipboardEvent<HTMLInputElement>) => {
		let text = e.clipboardData.getData('text').toString();
		if (!text.trim()) return;
		if (!text.split('\n').length && text.split(',').length && text.split('\t').length) return;
		e.preventDefault();
		let json = CSV.parse(text, {
			delimitersToGuess: [',', '\t'],
			dynamicTyping: true,
			fastMode: true
		} as CSV.ParseConfig) as CSV.ParseResult;
		if (json.data.length > 100) return console.error('Can\'t paste more than 100 players at once!');
		let data = json.data.map((row) => {
			let [firstName, lastName, rating]: [string, string, number] = row;
			if (!requireLastName) [firstName, rating, lastName] = [...row, ''];
			if (!firstName || (!lastName && requireLastName) || rating === null || rating === undefined) return null;
			try {
				if (typeof firstName !== 'string') throw [firstName, 'string'].join(',');
				if (typeof lastName !== 'string' && requireLastName) throw [lastName, 'string'].join(',');
				if (typeof rating !== 'number') throw [rating, 'number'].join(',');
				return {firstName, lastName, rating};
			} catch (error) {
				if (!text.split('\n').length) return null;
				let e = error.split(',');
				console.error(new TypeError('Bad input on row ' + row.join(',') + '\n' + e[0] + ' is not type ' + e[1]));
				return null;
			}
		})
			.filter(r => r);
		POST({
			url: 'tournament/' + props.id + '/appendPlayers',
			data
		})
			.then(() => Promise.all([props.updatePlayerDict(), props.setPlayers()]));
	}, [props.id, requireLastName, props.updatePlayerDict, props.setPlayers]);

	const Forms = useMemo((): JSX.Element[] => {

		let forms = [] as JSX.Element[];
		
		for (let i = 0; i < 5; i++) {
			forms.push(
				<EditableForm
					key={['newPlayerForm', i].join('.')}
					id={props.id}
					player={null}
					settings={props.settings}
					close={() => {}}
					onSubmit={(e: FormEvent<HTMLFormElement>): Promise<void> => FORM(e)
						.then(() => {
							let form = e.target as HTMLFormElement;
							form.reset();
						})
						.then(() => props.updatePlayerDict())
					}
					handlePaste={i ? undefined : handlePaste}
					action={['tournament', props.id, 'appendPlayer'].join('/')}
				/>
			);
		}
		return forms;
	}, [props.updatePlayerDict, props.id, requireLastName, handlePaste, props.settings.checkForDuplicatePlayers]);

	useEffect(() => {
		if (!props.playerDict.size) return;
		props.setPlayers(false);
	}, [props.playerDict, props.setPlayers]);

	useEffect(() => {
		if (!props.playerDict.size) return;
		props.setPlayers();
	}, [props.playerDict, props.playerDictKey, props.stateKey]);

	const [searchResults, setResults] = useState([] as (SearchProfile & { id: string })[]);
	const handleInput = useCallback(async (id: string) => {
		if (!id) {
			setResults([]);
			return [];
		}
		let parsed = isElectron ? await ECF.searchUsers(id, 5, true).catch((e) => {
			console.error(e);
			return [];
		}) as SearchProfile[] : await GET({
			url: ['fetch', 'ecf', 'searchUsers'].join('/'),
			params: { argument: id, nb: 5, parse: true } as FetchECFParams
		}) as SearchProfile[];
		setResults(parsed);
		return parsed;
	}, [searchResults, setResults]);

	const parseECFUser = useCallback((p: SearchProfile) => {
		return `${p.name} (S:${p.standard.rating}/R:${p.rapidplay.rating})\n${p.club}`;
	}, []);

	const setAlert = useContext(AlertContext);

	const handleECFsubmit = useCallback((id: string) => {
		let p = searchResults.find(p => p.id === id);
		if (!p) {
			console.error(id, p, searchResults);
			return setAlert({
				message: <>Couldn't add new player: {id}<br />Contact a developer if this problem persists</>,
				type: 'error'
			});
		}
		let rating = p.standard.rating || p.rapidplay.rating;
		POST({
			url: ['tournament', props.id, 'appendPlayer'].join('/'),
			data: {
				firstName: [p.firstName, p.middleName].join(' '),
				lastName: p.lastName,
				rating: props.settings.ratingSystem === 'FIDE' ? convertToFIDE(rating): rating
			}
		})
			.then(props.updatePlayerDict);
	}, [searchResults, setAlert, props.id])

	const [mode, setMode] = useState('ECF');

	const Section = useMemo(function Section() {
		switch (mode) {
			case 'new':
				return <div className={['scrollable', styles.playersSection].join(' ')}>
					<div className={styles.scoreSection}>
						{Forms}
					</div>
				</div>
			case 'ECF':
				return <Search
					onInput={handleInput}
					onSubmit={handleECFsubmit}
					options={[]}
					getName={(id) => parseECFUser(id as any as SearchProfile)}
					showHistory={false}
					styles={styles}
					placeholder='Last name, First name'
					limit={5}
				/>
			default:
				return null;
		}
	}, [mode, handleInput, handleECFsubmit])

	return (
		<>	
			<div className={[styles.sidebar, styles.newPlayers].join(' ')}>	
				<div className={styles.stage}>
					<TabSection
						name={mode}
						setName={setMode}
						nameDict={{
							new: <><FaPlusSquare/>New Player</>,
							ECF: <><img src={ecf} alt='' style={{height: '1.5em', filter: 'invert(0.5)'}} />ECF</>
						}}
						static
					/>
					{Section}					
				</div>
			</div>
		</>
	);
}